<template>
    <div>
        <div class="clearfix user">
            <div class="fl">
                <el-form :model="query">
                    <el-form-item>
                        <el-select v-model="query.default" placeholder="" size="small">
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                            <el-option label="ID" value="3"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-input placeholder="" v-model="query.leyword" size="small"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" size="small"></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="fr">
                <el-form>
                    <el-form-item>
                        <el-button type="primary" size="small" @click="createAccount = true"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <router-link :to="{path: 'home/roleList'}"><el-button type="primary" size="small" ></el-button></router-link>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" size="small"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="table-list">
            <el-table
                :data="tableListData"
                border
                style="width: 100%"
                >
                <el-table-column
                prop="userId"
                label="ID()"
                align="center">
                </el-table-column>
                <el-table-column
                prop="mobilePhone"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="name"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                prop="signature"
                label=""
                align="center">
                </el-table-column>
                <el-table-column
                label=""
                align="center">
                    <template slot-scope="scope">
                        <span v-if="scope.row.state === 1"></span>
                        <span v-if="scope.row.state === 0"></span>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                align="center">
                    <template slot-scope="scope">
                        {{compileDate(scope.row.recentLoginTime)}}
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                align="center">
                    <template slot-scope="scope">
                        <a href="javascript:" class="blue"></a>
                        <a href="javascript:" class="red ml20"></a>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="page">
            <el-pagination
            :page-size="pageSize"
            background
            layout="prev, pager, next,total"
            :total="total">
            </el-pagination>
        </div>
        <!--  --> 
        <el-dialog title="" :visible.sync="createAccount" width="25%" center :before-close="userClose">
            <el-form :model="createForm" size="small" :rules="rules" status-icon ref="createForm" label-width="70px" class="demo-ruleForm create-form">
                <el-form-item label="" prop="checkPhone">
                    <el-input type="text" v-model="createForm.checkPhone" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="pass">
                    <el-input type="password" v-model="createForm.pass" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="checkPass">
                    <el-input type="password" v-model="createForm.checkPass" autocomplete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="username">
                    <el-input v-model.number="createForm.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select class="w94" v-model="createAccountValue" placeholder="" size="small">
                        <el-option label="1 - VIP" value="1"></el-option>
                        <el-option label="2 - " value="2"></el-option>
                        <el-option label="3 - " value="3"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" size="small" @click="submitForm('createForm')"></el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {dateFor} from '../../api/utils'
export default {
    name:'adminUser',
    data(){
        //
        var checkPhone = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            } else {
            const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
            console.log(reg.test(value));
            if (reg.test(value)) {
                callback();
            } else {
                return callback(new Error(''));
            }
            }
        };
        //
        var pass = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            } else {
            const reg = /^[a-zA-Z]\w{5,17}$/
            console.log(reg.test(value));
            if (reg.test(value)) {
                callback();
            } else {
                return callback(new Error('6~18,'));
            }
            }
        };
        //
        let checkPass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''))
            } else if (value !== this.createForm.pass) {
                callback(new Error('!'))
            } else {
                callback()
            }
        };
        //
        var username = (rule, value, callback) => {
            if (!value) {
            return callback(new Error(''));
            } else {
            const reg = /^.{2,20}$/
            console.log(reg.test(value));
            if (reg.test(value)) {
                callback();
            } else {
                return callback(new Error(' 2  5 '));
            }
            }
        };
        return{
            total: 0,
            createAccount: false,
            createAccountValue: '',
            pageSize: 15,
            createForm: {
                checkPhone: '',
                pass: '',
                username:'',
                checkPass:''
            },
            tableListData:[],
            //
            query: {
                keyword: '',
                default: '1'
            },
            //
            rules: {
                checkPhone: [
                    {validator: checkPhone, trigger: 'blur'}
                ],
                pass: [
                    {validator: pass, trigger: 'blur'}
                ],
                checkPass: [
                    {validator: checkPass, trigger: 'blur'}
                ],
                username: [
                    {validator: username, trigger: 'blur'}
                ]
            }
        }
    },
    methods: {
        tableList(){
            let that = this;
            that.$axios.get('/adminUser').then((res)=>{
                that.tableListData = res.data[0].data;
                that.pageSize = res.data[0].page.pageSize;
                that.total = res.data[0].page.totalRows;
            })
        },
        userClose(done){
            done();
        },
        /*  */
        compileDate(time){
            return dateFor.dateFormat(time)
        },
        submitForm(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    },
    mounted(){
        this.tableList()
    }
}
</script>
<style lang="less" scoped>
.el-pagination{
    text-align: center;
}
.el-dialog__header{
    text-align: left;
}
.el-form-item--small.el-form-item{
    margin-bottom: 30px;
}
.el-dialog--center .el-dialog__body{
    padding: 5px 25px;
}
.create-form .el-form-item{
    width: 100%;
}
.el-form-item__content{
    width: 100%;
}
.w94{
    width: 94%;
}

</style>



